.home {
  .info-section {
    padding: 54px;
    width: 35%;
  }

  .content-area {
    background: $color3;
    margin-left: 35%;
    width: 65%;
    background: white;
    height: 100%;

    .img-container {
      display: inline-block;
      width: 30%;
      position: relative;
      height: 100%;

      .user-img {
        position: absolute;
        top: 56px;
        left: -250px;
        width: 500px;
        height: calc(100% - 112px);
        background: url("/img/model_img.jpg") no-repeat center center;
        -webkit-background-size: cover;
        -moz-background-size: cover;
        -o-background-size: cover;
        background-size: cover;
        -webkit-box-shadow: 9px 20px 74px 6px rgba(0, 0, 0, 0.34);
        -moz-box-shadow: 9px 20px 74px 6px rgba(0, 0, 0, 0.34);
        box-shadow: 9px 20px 74px 6px rgba(0, 0, 0, 0.34);
      }
    }

    .profile-section {
      vertical-align: top;
      display: inline-block;
      padding: 20% 0 0 0;
      max-width: 500px;

      .profile {
        h1 {
          font-size: 2.3rem;
          font-weight: 500;
          line-height: 1.2;
          margin: 0 0 1rem;

          span {
            color: $color1;
          }
        }

        h5 {
          font-size: 1.1rem;
          margin: 0 0 2rem;
        }

        p {
          color: $color5;
          font-weight: 300;
          line-height: 1.4;
          margin: 0 0 2rem;
        }
      }
    }
  }
}
